.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

body {
    color: #61dafb;
    padding: 10px;
}

img {
    margin: 10px;
}

#image-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

#headerBtn {
    color: white;
}

.NavLink {
    color: white;
    text-decoration: none;
}

.NavLink:hover, #headerBtn:hover {
    color: #349eeb;
}

#sign-up-column2 {
    margin-top: 25%;
}

.modal {
    border: 1px solid #ccc;
    box-shadow: 
      -2rem 2rem 2rem rgba(black, 0.2);
  }

li {
    list-style: none;
}

.active-user {
    color: black;
    font-weight: bold;
}

#modal-grid {
    display: flex;
    justify-content: center;
}

#side-column {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}